import Vue from 'vue';
import i18n from '../../../i18n';
import Progress from '../../../common/scripts/loading';
export default Vue.extend({
    i18n: i18n('desktop/views/pages/note.vue'),
    data() {
        return {
            fetching: true,
            note: null
        };
    },
    watch: {
        $route: 'fetch'
    },
    created() {
        this.fetch();
    },
    methods: {
        fetch() {
            Progress.start();
            this.fetching = true;
            this.$root.api('notes/show', {
                noteId: this.$route.params.note
            }).then(note => {
                this.note = note;
                this.fetching = false;
                Progress.done();
            });
        }
    }
});
